import React, { useState,useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import showImage from '../images/fight.png' ;
 function DashboardComponent(){
    const [error,setError] = useState(null);
    const [isLoaded,setIsLoaded] = useState(false);
    const [items,setItems] = useState([]);

    useEffect(() => {
        fetch("https://api.rootnet.in/covid19-in/stats/latest")
        .then( res => res.json())
        .then(
            (result) => {
              setIsLoaded(true);
              setItems(result);
              
              
            },
            (error) => {
                setIsLoaded(true);
                setError(error);
            }
        )
        

    }, [])

    var totalcases;
    var totalrecovered;
    var totaldeaths;
    if(items.length!=0)
    {
        totalcases= items.data["summary"].total;
        totalrecovered= items.data["summary"].discharged;
        totaldeaths= items.data["summary"].deaths;
        console.log(items.data["summary"]);
    }

return(
    <div className="container">
        <img src={showImage} style={{height:"200px",marginLeft: "auto",marginRight: "auto",display: "block",
        marginTop: "50px",marginBottom: "50px"}}>
        </img>
        <div class="card bg-info text-white">
            <div class="card-body">CASES IN INDIA UNTIL NOW</div>
        </div>
        <div class="card bg-light text-dark">
            <div class="card-body">
                <h4 style={{color:"#33DDFF"}}>Total Cases - {totalcases}</h4>
            </div>
        </div>
        <div class="card bg-light text-dark">
            <div class="card-body">
                <h4 style={{color:"limegreen"}}>Total Recovered - {totalrecovered}</h4>
            </div>
        </div>
        <div class="card bg-light text-dark">
            <div class="card-body">
                <h4 style={{color:"#E50000"}} >Total Deaths - {totaldeaths}</h4>
            </div>
        </div>


    </div>
)
 }
 export default DashboardComponent;